// CodecsHandler.js

const splitLines = (sdp) => {
  const info = {};
  sdp.split('\n').forEach((line) => {
    if (line.indexOf('m=video') === 0) {
      info.videoCodecNumbers = [];
      line.split('SAVPF')[1].split(' ').forEach((codecNumber) => {
        codecNumber = codecNumber.trim();
        if (!codecNumber || !codecNumber.length) return;
        info.videoCodecNumbers.push(codecNumber);
        info.videoCodecNumbersOriginal = line;
      });
    }

    if (line.indexOf('VP8/90000') !== -1 && !info.vp8LineNumber) {
      info.vp8LineNumber = line.replace('a=rtpmap:', '').split(' ')[0];
    }

    if (line.indexOf('VP9/90000') !== -1 && !info.vp9LineNumber) {
      info.vp9LineNumber = line.replace('a=rtpmap:', '').split(' ')[0];
    }

    if (line.indexOf('H264/90000') !== -1 && !info.h264LineNumber) {
      info.h264LineNumber = line.replace('a=rtpmap:', '').split(' ')[0];
    }
  });

  return info;
};

const preferCodecHelper = (sdp, codec, info, ignore) => {
  let preferCodecNumber = '';

  if (codec === 'vp8') {
    if (!info.vp8LineNumber) {
      return sdp;
    }
    preferCodecNumber = info.vp8LineNumber;
  }

  if (codec === 'vp9') {
    if (!info.vp9LineNumber) {
      return sdp;
    }
    preferCodecNumber = info.vp9LineNumber;
  }

  if (codec === 'h264') {
    if (!info.h264LineNumber) {
      return sdp;
    }

    preferCodecNumber = info.h264LineNumber;
  }

  let newLine = `${info.videoCodecNumbersOriginal.split('SAVPF')[0]}SAVPF `;

  let newOrder = [preferCodecNumber];

  if (ignore) {
    newOrder = [];
  }

  info.videoCodecNumbers.forEach((codecNumber) => {
    if (codecNumber === preferCodecNumber) return;
    newOrder.push(codecNumber);
  });

  newLine += newOrder.join(' ');

  sdp = sdp.replace(info.videoCodecNumbersOriginal, newLine);
  return sdp;
};

export const preferCodec = (sdp, codecName) => {
  const info = splitLines(sdp);

  codecName = codecName.toLowerCase();

  if (!info.videoCodecNumbers) {
    return sdp;
  }

  if (codecName === 'vp8' && info.vp8LineNumber === info.videoCodecNumbers[0]) {
    return sdp;
  }

  if (codecName === 'vp9' && info.vp9LineNumber === info.videoCodecNumbers[0]) {
    return sdp;
  }

  if (codecName === 'h264' && info.h264LineNumber === info.videoCodecNumbers[0]) {
    return sdp;
  }

  sdp = preferCodecHelper(sdp, codecName, info);

  return sdp;
};

export const removeVPX = (sdp) => {
  const info = splitLines(sdp);

  // last parameter below means: ignore these codecs
  sdp = preferCodecHelper(sdp, 'vp9', info, true);
  sdp = preferCodecHelper(sdp, 'vp8', info, true);

  return sdp;
};

export const disableNACK = (sdp) => {
  if (!sdp || typeof sdp !== 'string') {
    throw new Error('Invalid arguments.');
  }

  sdp = sdp.replace('a=rtcp-fb:126 nack\r\n', '');
  sdp = sdp.replace('a=rtcp-fb:126 nack pli\r\n', 'a=rtcp-fb:126 pli\r\n');
  sdp = sdp.replace('a=rtcp-fb:97 nack\r\n', '');
  sdp = sdp.replace('a=rtcp-fb:97 nack pli\r\n', 'a=rtcp-fb:97 pli\r\n');

  return sdp;
};

export const prioritize = (codecMimeType, peer) => {
  if (!peer || !peer.getSenders || !peer.getSenders().length) {
    return;
  }

  if (!codecMimeType || typeof codecMimeType !== 'string') {
    throw new Error('Invalid arguments.');
  }

  peer.getSenders().forEach((sender) => {
    const params = sender.getParameters();
    for (let i = 0; i < params.codecs.length; i++) {
      if (params.codecs[i].mimeType === codecMimeType) {
        params.codecs.unshift(params.codecs.splice(i, 1));
        break;
      }
    }
    sender.setParameters(params);
  });
};

export const removeNonG722 = (sdp) => (
  sdp.replace(/m=audio ([0-9]+) RTP\/SAVPF ([0-9 ]*)/g, 'm=audio $1 RTP/SAVPF 9')
);


const setBAS = (sdp, bandwidth, isScreen) => {
  if (!!navigator.mozGetUserMedia || !bandwidth) {
    return sdp;
  }

  if (isScreen) {
    if (!bandwidth.screen) {
      console.warn(`
        It seems that you are not using bandwidth for screen.
        Screen sharing is expected to fail.
    `);
    } else if (bandwidth.screen < 300) {
      console.warn(`
        It seems that you are using wrong bandwidth value for screen.
        Screen sharing is expected to fail.
      `);
    }
  }

  // if screen; must use at least 300kbs
  if (bandwidth.screen && isScreen) {
    sdp = sdp.replace(/b=AS([^\r\n]+\r\n)/g, '');
    sdp = sdp.replace(/a=mid:video\r\n/g, `a=mid:video\r\nb=AS:${bandwidth.screen}\r\n`);
  }

  // remove existing bandwidth lines
  if (bandwidth.audio || bandwidth.video || bandwidth.data) {
    sdp = sdp.replace(/b=AS([^\r\n]+\r\n)/g, '');
  }

  if (bandwidth.audio) {
    sdp = sdp.replace(/a=mid:audio\r\n/g, `a=mid:audio\r\nb=AS:${bandwidth.audio}\r\n`);
  }

  if (bandwidth.video) {
    const regex = /a=mid:video\r\n/g;
    sdp = sdp.replace(regex, `
      a=mid:video
      b=AS:${isScreen ? bandwidth.screen : bandwidth.video}
    `);
  }

  return sdp;
};

// Find the line in sdpLines[startLine...endLine - 1] that starts with |prefix|
// and, if specified, contains |substr| (case-insensitive search).
const findLineInRange = (sdpLines, startLine, endLine, prefix, substr) => {
  const realEndLine = endLine !== -1 ? endLine : sdpLines.length;
  for (let i = startLine; i < realEndLine; ++i) {
    if (sdpLines[i].indexOf(prefix) === 0) {
      if (!substr || sdpLines[i].toLowerCase().indexOf(substr.toLowerCase()) !== -1) {
        return i;
      }
    }
  }
  return null;
};

// Find the line in sdpLines that starts with |prefix|, and, if specified,
// contains |substr| (case-insensitive search).
const findLine = (sdpLines, prefix, substr) => (
  findLineInRange(sdpLines, 0, -1, prefix, substr)
);

// Gets the codec payload type from an a=rtpmap:X line.
const getCodecPayloadType = (sdpLine) => {
  const pattern = new RegExp('a=rtpmap:(\\d+) \\w+\\/\\d+');
  const result = sdpLine.match(pattern);
  return result && result.length === 2 ? result[1] : null;
};

const _setVideoBitrates = (sdp, params) => {
  params = params || {};
  const xgoogleMinBitrate = params.min;
  const xgoogleMaxBitrate = params.max;
  const videoCodec = params.codec.toUpperCase() || 'VP8';

  const sdpLines = sdp.split('\r\n');

  // VP8
  const vp8Index = findLine(sdpLines, 'a=rtpmap', `${videoCodec}/90000`);
  let vp8Payload;
  if (vp8Index) {
    vp8Payload = getCodecPayloadType(sdpLines[vp8Index]);
  }

  if (!vp8Payload) {
    return sdp;
  }

  const rtxIndex = findLine(sdpLines, 'a=rtpmap', 'rtx/90000');
  let rtxPayload;
  if (rtxIndex) {
    rtxPayload = getCodecPayloadType(sdpLines[rtxIndex]);
  }

  if (!rtxIndex) {
    return sdp;
  }

  const rtxFmtpLineIndex = findLine(sdpLines, `a=fmtp:${rtxPayload.toString()}`);
  if (rtxFmtpLineIndex != null) {
    let appendrtxNext = '\r\n';
    appendrtxNext += `a=fmtp:${vp8Payload} x-google-min-bitrate=${xgoogleMinBitrate || '228'}; x-google-max-bitrate=${xgoogleMaxBitrate || '228'}`;
    sdpLines[rtxFmtpLineIndex] = sdpLines[rtxFmtpLineIndex].concat(appendrtxNext);
    sdp = sdpLines.join('\r\n');
  }

  return sdp;
};

const _setOpusAttributes = (sdp, params) => {
  params = params || {};

  const sdpLines = sdp.split('\r\n');

  // Opus
  const opusIndex = findLine(sdpLines, 'a=rtpmap', 'opus/48000');
  let opusPayload;
  if (opusIndex) {
    opusPayload = getCodecPayloadType(sdpLines[opusIndex]);
  }

  if (!opusPayload) {
    return sdp;
  }

  const opusFmtpLineIndex = findLine(sdpLines, `a=fmtp:${opusPayload.toString()}`);
  if (opusFmtpLineIndex == null) {
    return sdp;
  }

  const stereo = typeof params.stereo !== 'undefined' ? params.stereo : '1';
  const spropStereo = typeof params['sprop-stereo'] !== 'undefined' ? params['sprop-stereo'] : '1';

  let appendOpusNext = '';
  appendOpusNext += `; stereo=${stereo}`;
  appendOpusNext += `; sprop-stereo=${spropStereo}`;

  if (typeof params.maxaveragebitrate !== 'undefined') {
    appendOpusNext += `; maxaveragebitrate=${params.maxaveragebitrate || 128 * 1024 * 8}`;
  }

  if (typeof params.maxplaybackrate !== 'undefined') {
    appendOpusNext += `; maxplaybackrate=${params.maxplaybackrate || 128 * 1024 * 8}`;
  }

  if (typeof params.cbr !== 'undefined') {
    appendOpusNext += `; cbr=${typeof params.cbr !== 'undefined' ? params.cbr : '1'}`;
  }

  if (typeof params.useinbandfec !== 'undefined') {
    appendOpusNext += `; useinbandfec=${params.useinbandfec}`;
  }

  if (typeof params.usedtx !== 'undefined') {
    appendOpusNext += `; usedtx=${params.usedtx}`;
  }

  if (typeof params.maxptime !== 'undefined') {
    appendOpusNext += `\r\na=maxptime:${params.maxptime}`;
  }

  sdpLines[opusFmtpLineIndex] = sdpLines[opusFmtpLineIndex].concat(appendOpusNext);

  sdp = sdpLines.join('\r\n');
  return sdp;
};

// forceStereoAudio => via webrtcexample.com
// requires getUserMedia => echoCancellation:false
export const forceStereoAudio = (sdp) => {
  const sdpLines = sdp.split('\r\n');
  let opusPayload;
  let fmtpLineIndex = null;
  for (let i = 0; i < sdpLines.length; i++) {
    if (sdpLines[i].search('opus/48000') !== -1) {
      opusPayload = extractSdp(sdpLines[i], /:(\d+) opus\/48000/i);
      break;
    }
  }
  for (let i = 0; i < sdpLines.length; i++) {
    if (sdpLines[i].search('a=fmtp') !== -1) {
      const payload = extractSdp(sdpLines[i], /a=fmtp:(\d+)/);
      if (payload === opusPayload) {
        fmtpLineIndex = i;
        break;
      }
    }
  }
  if (fmtpLineIndex == null) return sdp;
  sdpLines[fmtpLineIndex] = sdpLines[fmtpLineIndex].concat('; stereo=1; sprop-stereo=1');
  sdp = sdpLines.join('\r\n');
  return sdp;
};

export const setApplicationSpecificBandwidth = (sdp, bandwidth, isScreen) => (
  setBAS(sdp, bandwidth, isScreen)
);

export const setVideoBitrates = (sdp, params) => _setVideoBitrates(sdp, params);

export const setOpusAttributes = (sdp, params) => _setOpusAttributes(sdp, params);

export const preferVP9 = (sdp) => preferCodec(sdp, 'vp9');
